0px;\n            display: flex;\n            align-items: center;\n            justify-content: center;\n            margin: auto;\n            input {\n                position: absolute;\n                width: $hitboxWidth;\n                height: $hitboxHeight;\n                margin: 0;\n                &[name$=\"0\"] { left: 0; top: 0; }\n                &[name$=\"1\"] { left: calc(50% - #{$hitboxWidth}/2); top: 0; }\n                &[name$=\"2\"] { left: calc(100% - #{$hitboxWidth}); top: 0; }\n                &[name$=\"3\"] { left: calc(100% - #{$hitboxWidth}); top: calc(50% - #{$hitboxHeight}/2); }\n                &[name$=\"4\"] { left: calc(100% - #{$hitboxWidth}); top: calc(100% - #{$hitboxHeight}); }\n                &[name$=\"5\"] { left: calc(50% - #{$hitboxWidth}/2); top: calc(100% - #{$hitboxHeight}); }\n                &[name$=\"6\"] { left: 0; top: calc(100% - #{$hitboxHeight}); }\n                &[name$=\"7\"] { left: 0; top: calc(50% - #{$hitboxHeight}/2); }\n            }\n        }\n        .transition-caption {\n            text-align: center;\n        }\n    }\n}\n\n#world-panel {\n    top: $worldPanelTop;\n    left: $worldPanelLeft;\n    width: $worldPanelWidth;\n    height: $worldPanelHeight;\n}\n\n.panel-body {\n    position: relative;\n    label {\n        font-size: 0.8em;\n    }\n    input[type=\"number\"] {\n        width: 70px;\n    }\n    select {\n        width: 80px;\n    }\n    .list {\n      height: 115px;\n      overflow: auto;\n    }\n}\n\n.hidden {\n    display: none !important;\n}\n.tool-descriptor {\n    float: right;\n    height: 20px;\n    .selected-tile {\n        display : flex;\n        align-items : center;\n        height: 100%;\n        &>span {\n            margin-right: 4px;\n        }\n        .selected-tile-sprite {\n            position: relative;\n            width: 16px;\n            height: 16px;\n            display: inline-block;\n        }\n    }\n}\n\n#panels-container .panel-body {\n    height: cal